import disposableEvent from "disposable-event";
import { CompositeDisposable, Emitter } from "sb-event-kit"; // Emitter of sb-event-kit is necessary for commands

import { Disposable } from "atom";
import type { TextEditor } from "atom";
import type { ListMovement } from "./types";
import type { CommandEventExtended } from "./types/atom";
import { TargetWithAddEventListener } from "disposable-event/src/target";
import { showError } from "./helpers"; // NOTE:
// We don't *need* to add the intentions:hide command
// But we're doing it anyway because it helps us keep the code clean
// And can also be used by any other package to fully control this package
// List of core commands we allow during the list, everything else closes it

const CORE_COMMANDS = new Set(["core:move-up", "core:move-down", "core:page-up", "core:page-down", "core:move-to-top", "core:move-to-bottom"]);
type ShowListEvent = {
  show: boolean;
  editor: TextEditor;
};
export class Commands {
  active: {
    type: "list" | "highlight";
    subscriptions: CompositeDisposable;
  } | null = null;
  emitter = new Emitter<{}, // eslint-disable-line @typescript-eslint/ban-types
  {
    "list-show": ShowListEvent;
    "list-move": ListMovement;
    "list-confirm": never;
    "list-hide": never;
    "highlights-show": ShowListEvent;
    "highlights-hide": never;
  }>();
  subscriptions = new CompositeDisposable();

  constructor() {
    this.subscriptions.add(this.emitter);
  }

  activate() {
    this.subscriptions.add(atom.commands.add("atom-text-editor:not([mini])", {
      "intentions:show": async (e: CommandEventExtended) => {
        if (this.active?.type === "list") {
          return;
        }

        const subscriptions = new CompositeDisposable();
        const processListShowP = this.processListShow(subscriptions);

        if (e.originalEvent?.type !== "keydown") {
          return;
        }

        setImmediate(() => {
          let matched = true;
          subscriptions.add(atom.keymaps.onDidMatchBinding(function ({
            binding
          }) {
            matched = matched && CORE_COMMANDS.has(binding.command);
          }), disposableEvent(((document.body as unknown) as TargetWithAddEventListener), "keyup", () => {
            if (matched) {
              return;
            }

            subscriptions.dispose();
            this.processListHide();
          }));
        });
        await processListShowP;
      },
      "intentions:hide": this.processListHide,
      "intentions:highlight": async (e: CommandEventExtended<KeyboardEvent>) => {
        if (this.active?.type === "highlight") {
          return;
        }

        e.abortKeyBinding();
        const subscriptions = new CompositeDisposable();
        const processHighlightsShowP = this.processHighlightsShow(subscriptions);

        if (e.originalEvent?.type !== "keydown") {
          return;
        }

        const {
          keyCode
        } = e.originalEvent;
        subscriptions.add(disposableEvent(((document.body as unknown) as TargetWithAddEventListener), "keyup", (upE: KeyboardEvent) => {
          if (upE.keyCode !== keyCode) {
            return;
          }

          subscriptions.dispose();
          this.processHighlightsHide();
        }));
        await processHighlightsShowP;
      }
    }), atom.commands.add("atom-text-editor.intentions-list:not([mini])", {
      "intentions:confirm": this.stoppingEvent(this.processListConfirm),
      "core:move-up": this.stoppingEvent(() => this.processListMove("up")),
      "core:move-down": this.stoppingEvent(() => this.processListMove("down")),
      "core:page-up": this.stoppingEvent(() => this.processListMove("page-up")),
      "core:page-down": this.stoppingEvent(() => this.processListMove("page-down")),
      "core:move-to-top": this.stoppingEvent(() => this.processListMove("move-to-top")),
      "core:move-to-bottom": this.stoppingEvent(() => this.processListMove("move-to-bottom"))
    }));
  }

  stoppingEvent(callback: (event: Event) => void): (event: Event) => void {
    return (event: Event) => {
      event.stopImmediatePropagation();
      callback.call(this, event);
    };
  }

  async processListShow(subscription: (CompositeDisposable | Disposable) | null | undefined = null) {
    if (this.active !== null) {
      switch (this.active.type) {
        case "list":
          throw new Error("Already active");

        case "highlight":
          this.processHighlightsHide();
          break;

        default:
      }
    }

    const editor = atom.workspace.getActiveTextEditor();

    if (editor === undefined) {
      return;
    }

    const editorElement = atom.views.getView(editor);
    const subscriptions = new CompositeDisposable();

    if (subscription) {
      subscriptions.add(subscription);
    }

    if (!(await this.shouldListShow(editor))) {
      return;
    }

    this.active = {
      type: "list",
      subscriptions
    };
    subscriptions.add(() => {
      if (this.active !== null && this.active.type === "list" && this.active.subscriptions === subscriptions) {
        this.processListHide();
        this.active = null;
      }

      editorElement.classList.remove("intentions-list");
    }, disposableEvent(((document.body as unknown) as TargetWithAddEventListener), "mouseup", function () {
      setTimeout(function () {
        subscriptions.dispose();
      }, 10);
    }));
    editorElement.classList.add("intentions-list");
  }

  processListHide() {
    if (this.active?.type !== "list") {
      return;
    }

    const {
      subscriptions
    } = this.active;
    this.active = null;
    subscriptions.dispose();
    this.emitter.emit("list-hide").catch((e: Error) => showError(e));
  }

  processListMove(movement: ListMovement) {
    if (this.active?.type !== "list") {
      return;
    }

    this.emitter.emit("list-move", movement).catch((e: Error) => showError(e));
  }

  processListConfirm() {
    if (this.active?.type !== "list") {
      return;
    }

    this.emitter.emit("list-confirm").catch((e: Error) => showError(e));
  }

  async processHighlightsShow(subscription: (CompositeDisposable | Disposable) | null | undefined = null) {
    if (this.active !== null) {
      switch (this.active.type) {
        case "highlight":
          throw new Error("Already active");

        case "list":
          this.processListHide();
          break;

        default:
      }
    }

    const editor = atom.workspace.getActiveTextEditor();

    if (editor === undefined) {
      return;
    }

    const editorElement = atom.views.getView(editor);
    const subscriptions = new CompositeDisposable();
    const shouldProcess = await this.shouldHighlightsShow(editor);

    if (subscription) {
      subscriptions.add(subscription);
    }

    if (!shouldProcess) {
      return;
    }

    this.active = {
      type: "highlight",
      subscriptions
    };
    subscriptions.add(() => {
      if (this.active !== null && this.active.type === "highlight" && this.active.subscriptions === subscriptions) {
        this.processHighlightsHide();
      }

      editorElement.classList.remove("intentions-highlights");
    });
    editorElement.classList.add("intentions-highlights");
  }

  processHighlightsHide() {
    if (this.active?.type !== "highlight") {
      return;
    }

    const {
      subscriptions
    } = this.active;
    this.active = null;
    subscriptions.dispose();
    this.emitter.emit("highlights-hide").catch((e: Error) => showError(e));
  }

  async shouldListShow(editor: TextEditor): Promise<boolean> {
    const event: ShowListEvent = {
      show: false,
      editor
    };
    await this.emitter.emit("list-show", event);
    return event.show;
  }

  async shouldHighlightsShow(editor: TextEditor): Promise<boolean> {
    const event: ShowListEvent = {
      show: false,
      editor
    };
    await this.emitter.emit("highlights-show", event);
    return event.show;
  }

  onListShow(callback: (editor: TextEditor) => Promise<boolean>) {
    return this.emitter.on("list-show", async (event: ShowListEvent) => {
      const result = await callback(event.editor);
      /* eslint-disable-next-line require-atomic-updates */

      event.show = Boolean(result);
    });
  }

  onListHide(callback: () => void) {
    return this.emitter.on("list-hide", callback);
  }

  onListMove(callback: (movement: ListMovement) => void) {
    return this.emitter.on("list-move", callback);
  }

  onListConfirm(callback: () => void) {
    return this.emitter.on("list-confirm", callback);
  }

  onHighlightsShow(callback: (editor: TextEditor) => Promise<boolean>) {
    return this.emitter.on("highlights-show", async (event: ShowListEvent) => {
      const result = await callback(event.editor);
      /* eslint-disable-next-line require-atomic-updates */

      event.show = Boolean(result);
    });
  }

  onHighlightsHide(callback: () => void) {
    return this.emitter.on("highlights-hide", callback);
  }

  dispose() {
    this.subscriptions.dispose();
    this.active?.subscriptions.dispose();
  }

}